body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #343A40;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@keyframes lds-double-ring {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@-webkit-keyframes lds-double-ring {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes lds-double-ring_reverse {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
  }
}
@-webkit-keyframes lds-double-ring_reverse {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
  }
}

.lds-css {
  margin: 0 auto;
}

.lds-double-ring {
  position: relative;
}
.lds-double-ring div {
  position: absolute;
  width: 160px;
  height: 160px;
  top: 20px;
  left: 20px;
  border-radius: 50%;
  border: 8px solid #000;
  border-color: #3d7ec0 transparent #3d7ec0 transparent;
  -webkit-animation: lds-double-ring 1.5s linear infinite;
  animation: lds-double-ring 1.5s linear infinite;
}
.lds-double-ring div:nth-child(2) {
  width: 140px;
  height: 140px;
  top: 30px;
  left: 30px;
  border-color: transparent #cc4545 transparent #cc4545;
  -webkit-animation: lds-double-ring_reverse 1.5s linear infinite;
  animation: lds-double-ring_reverse 1.5s linear infinite;
}
.lds-double-ring {
  width: 77px !important;
  height: 77px !important;
  -webkit-transform: translate(-38.5px, -38.5px) scale(0.385) translate(38.5px, 38.5px);
  transform: translate(-38.5px, -38.5px) scale(0.385) translate(38.5px, 38.5px);
}
.error-indicator {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  color:#4A76A8;
}

.error-indicator img {
  margin-bottom: 1rem;
}

.error-indicator .boom {
  font-size: 1.7rem;
}


.sortable-table {
  background-color: #5C656E;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 17px;
  color: #000;
  text-align: center;
  width: 75%;
  padding: 32px; }

.data-button {
  width: 100%;
  display: flex;
  justify-content: space-around; }

.spinner-item {
  display: flex;
  justify-content: center;
  background-color: #5C656E;
  width: 75%; }

.sorting-block {
  position: relative;
  padding-right: 20px !important;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }
  .sorting-block.is-sortASC:before {
    border-bottom-color: #333; }
  .sorting-block.is-sortDESC:after {
    border-top-color: #333; }
  .sorting-block:before, .sorting-block:after {
    position: absolute;
    content: '';
    right: 5px; }
  .sorting-block:before {
    top: 50%;
    margin-top: -5px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 5px 5px 5px;
    border-color: transparent transparent #eee transparent; }
  .sorting-block:after {
    top: 50%;
    margin-top: 1px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 5px 5px 0 5px;
    border-color: #eee transparent transparent transparent; }

.add-feed{
	width: 75%;
	padding: 0 15px;
	margin-bottom: 20px;
	height: 120px;
	background-color: #5C656E;
	display: flex;
	align-items: center;
	box-shadow: 0 0px 7px rgba(0,0,0,0.2);
	border-radius: 5px

}

@media (min-width: 768px) {
	.add-feed{
		width: 75%;
	}
}

.add-form{
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	height: 85%;
	flex-wrap: wrap;
}


.add-action{
	height: 20%;
	width: 30%;
	margin-top: 7px;
}

.btn-cancel{
	margin-left: 20px;
}

.btn-add-feed:hover{
	background-color: #00BC8C;
	border-color: #00BC8C;
	box-shadow: 0 0px 3px rgba(0,0,0,0.5);
}

.add-input{
	border-radius: 4px;
	margin-top: 8px;
	border: none;
	height: 30px;
	width: 30%;
}

.btn-create-feed{
	margin-bottom: 20px;
}
.item{
	width: 75%;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	background-color: #5C656E;
	color: #fff;
}
